import axios from "axios";
import { getApiUrl } from "../environment/environment";

import { DateRequest } from '../interface/date-request'
import { Venta } from "../interface/venta";

export const VentasService = {

    async getData() {
        const e = await axios.get(getApiUrl('list'));
        return e.data;
    },

    async getChartFiltered() {
        return axios.get(getApiUrl('reuest/chart')).then((e) => { return e.data })
    },

    async requestData(dateRequest: DateRequest) {
        return axios.post(getApiUrl('reuest'), dateRequest).then((e) => { return e.data })
    },

    async getChartFilteredDate(dateRequest: DateRequest) {
        return axios.post(getApiUrl('reuest/chart/final'), dateRequest).then((e) => { return e.data })
    },

    async postData(venta: Venta) {
        const response = await axios.post(getApiUrl('save'), venta);
        return response.data;
    },

    async updateData(idVenta: number, venta: Venta) {
        const response = await axios.put(getApiUrl(`update/${idVenta}`), venta);
        return response.data;
    },

    async delete(id: number) {
        const response = await axios.delete(getApiUrl(`delete/${id}`));
        return response.data;
    },
};