import React, { useState, useEffect } from "react";
import { Venta } from "../../interface/venta";
import { useOverlayHook } from "../../hooks/use-overlay";
import { VentasService } from "../../service/venta-service";
import {
    Button,
    Modal,
    ModalBody,
    ModalCloseButton,
    ModalContent,
    ModalFooter,
    ModalHeader,
    FormControl,
    FormLabel,
    Select,
} from "@chakra-ui/react";
import { TModalProps } from "./interface/t-modal-props";

export const ModalComponent = ({
    visible,
    setVisible,
    selectedVenta,
    onSuccess,
}: TModalProps<Venta>) => {
    const { overlay } = useOverlayHook();

    const [formData, setFormData] = useState<Venta>({
        idVenta: 0,
        fecha: new Date(),
        concecionario: "",
        almacen: "",
        cantidad: 1,
        estadoVenta: "",
    });

    const concecionarioOptions = [
        "AUTOCENTER MOTORS",
        "SPEEDY AUTOS",
        "ELITE CAR DEALERS",
        "TURBO MOTORS",
        "DRIVEWAY AUTOS",
    ];
    const almacenOptions = [
        "CENTRAL WAREHOUSE",
        "NORTHSIDE DEPOT",
        "WEST END STORAGE",
        "MAIN DISTRIBUTION CENTER",
        "SOUTH LOGISTICS HUB",
    ];
    const estadoVentaOptions = ["COMPLETA", "INCOMPLETA"];
    const cantidadOptions = [1, -1];

    useEffect(() => {
        if (selectedVenta) {
            setFormData({
                idVenta: selectedVenta.idVenta,
                fecha: selectedVenta.fecha,
                concecionario: selectedVenta.concecionario,
                almacen: selectedVenta.almacen,
                cantidad: selectedVenta.cantidad,
                estadoVenta: selectedVenta.estadoVenta,
            });
        }
    }, [selectedVenta]);

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleSave = async () => {
        try {
            if (formData.idVenta === 0) {
                await VentasService.postData(formData);
                onSuccess();
            } else {
                await VentasService.updateData(formData.idVenta, formData);
                onSuccess();
            }
            setVisible(false);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <Modal isCentered isOpen={visible} onClose={() => setVisible(false)}>
            {overlay}
            <ModalContent>
                <ModalHeader>
                    {formData.idVenta === 0 ? "Registrar Venta" : "Actualizar Venta"}
                </ModalHeader>
                <ModalCloseButton />
                <ModalBody>
                    <FormControl id="concecionario" mb={4}>
                        <FormLabel>Concesionario</FormLabel>
                        <Select
                            name="concecionario"
                            value={formData.concecionario}
                            onChange={handleChange}
                        >
                            <option value="">Selecciona un concesionario</option>
                            {concecionarioOptions.map((option, index) => (
                                <option key={index} value={option}>
                                    {option}
                                </option>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl id="almacen" mb={4}>
                        <FormLabel>Almacén</FormLabel>
                        <Select name="almacen" value={formData.almacen} onChange={handleChange}>
                            <option value="">Selecciona un almacén</option>
                            {almacenOptions.map((option, index) => (
                                <option key={index} value={option}>
                                    {option}
                                </option>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl id="cantidad" mb={4}>
                        <FormLabel>Cantidad</FormLabel>
                        <Select name="cantidad" value={formData.cantidad} onChange={handleChange}>
                            {cantidadOptions.map((option, index) => (
                                <option key={index} value={option}>
                                    {option}
                                </option>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl id="estadoVenta" mb={4}>
                        <FormLabel>Estado de la Venta</FormLabel>
                        <Select
                            name="estadoVenta"
                            value={formData.estadoVenta}
                            onChange={handleChange}
                        >
                            <option value="">Selecciona un estado</option>
                            {estadoVentaOptions.map((option, index) => (
                                <option key={index} value={option}>
                                    {option}
                                </option>
                            ))}
                        </Select>
                    </FormControl>
                </ModalBody>
                <ModalFooter>
                    <Button colorScheme="teal" mr={3} onClick={handleSave}>
                        {formData.idVenta === 0 ? "Guardar" : "Actualizar"}
                    </Button>
                    <Button onClick={() => setVisible(false)}>Cancelar</Button>
                </ModalFooter>
            </ModalContent>
        </Modal>
    );
};
