import React from "react";
import { InputProps } from "./interface/input-props";
import { Input } from "@chakra-ui/react";

export const InputComponent = (props: InputProps) => {
    return (
        <Input
            placeholder={props.placeholder}
            size={props.size}
            type={"datetime-local"}
            value={props.value}
            onChange={props.onChange}
        />
    );
};
