import React, { useEffect, useState } from "react";
import { Venta } from "../interface/venta";
import { VentasService } from "../service/venta-service";
import { DateRequest } from "../interface/date-request";
import { Box, Button, IconButton, Input, SimpleGrid } from "@chakra-ui/react";
import { DataFilterChart } from "../interface/data-chart";
import { useActiveHook } from "../hooks/use-active-hook";
import { Table } from "../components/Table";
import { ChartVenta } from "../components/ChartVenta";
import { TableSale } from "../components/TableSale";
import { ModalComponent } from "../components/modal/ModalComponent";
import { InputComponent } from "../components/input/InputComponent";
import { AddIcon, RepeatClockIcon, SettingsIcon } from "@chakra-ui/icons";

export const HomePage = () => {
    let requestEmpty: DateRequest = {
        startDate: null,
        endDate: null,
    };

    const [ventas, setVentas] = useState<Venta[]>([]);
    const [startDate, setStartDate] = useState<any>(null);
    const [endDate, setEndDate] = useState<any>(null);
    const [request, setRequest] = useState<DateRequest>(requestEmpty);
    const [dataChartAll, setDataChartAll] = useState<DataFilterChart[]>([]);
    const [selectedVenta, setSelectedVenta] = useState<Venta | null>(null);
    const [fetch, setFetch] = useState<boolean>(false);

    const fetchDataAll = async () => {
        try {
            const dataResponse = await VentasService.getData();
            const dataResponseChart = await VentasService.getChartFiltered();
            setVentas(dataResponse);
            setDataChartAll(dataResponseChart);
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        fetchDataAll();
    }, [fetch]);

    const total: number[] = dataChartAll.map((i) => i.total);
    const completo: number[] = dataChartAll.map((i) => i.completo);
    const incompleto: number[] = dataChartAll.map((i) => i.incompleto);
    const concesionario: string[] = dataChartAll.map((i) => i.concecionario);

    const handleRequest = () => {
        VentasService.requestData(request)
            .then((e) => {
                setVentas(e);
            })
            .catch((err) => {});

        VentasService.getChartFilteredDate(request)
            .then((e) => {
                setDataChartAll(e);
            })
            .catch((err) => {});
    };

    useEffect(() => {
        setRequest({ startDate, endDate });
    }, [startDate, endDate]);

    const { active, handleToggle } = useActiveHook();

    const handleEdit = (venta: Venta) => {
        setSelectedVenta(venta);
        handleToggle();
    };

    const handleCreate = () => {
        setSelectedVenta(null);
        handleToggle();
    };

    const handleRemove = async (id: number) => {
        await VentasService.delete(id);
        fetchDataAll();
    };

    return (
        <>
            <SimpleGrid columns={2} spacingX="40px" spacingY="20px" p={5}>
                <Box>
                    <InputComponent
                        placeholder="Select Date and Time"
                        size="md"
                        value={startDate}
                        onChange={(e) => setStartDate(e.target.value)}
                    />
                </Box>
                <Box>
                    <InputComponent
                        placeholder="Select Date and Time"
                        size="md"
                        value={startDate}
                        onChange={(e) => setEndDate(e.target.value)}
                    />
                </Box>
            </SimpleGrid>
            <div className="data p-3">
                <Button
                    leftIcon={<SettingsIcon />}
                    colorScheme="teal"
                    variant="solid"
                    onClick={handleRequest}
                >
                    Get dates
                </Button>

                <Button
                    leftIcon={<AddIcon />}
                    colorScheme="pink"
                    variant="solid"
                    onClick={handleCreate}
                    ml={2}
                >
                    New
                </Button>

                <IconButton
                    ml={2}
                    variant="outline"
                    colorScheme="teal"
                    aria-label="Send email"
                    onClick={() => fetchDataAll()}
                    icon={<RepeatClockIcon />}
                />

                <Table ventas={ventas} onEdit={handleEdit} onDelete={handleRemove} />
            </div>

            <SimpleGrid columns={2} spacingX="40px" spacingY="20px" p={5}>
                <Box>
                    {ventas && ventas ? (
                        <ChartVenta
                            total={total}
                            completo={completo}
                            incompleto={incompleto}
                            concecionario={concesionario}
                        />
                    ) : (
                        <></>
                    )}
                </Box>
                <Box>
                    <TableSale data={dataChartAll} />
                </Box>
            </SimpleGrid>

            <ModalComponent
                visible={active}
                setVisible={handleToggle}
                selectedVenta={selectedVenta}
                onSuccess={() => fetchDataAll()}
            />
        </>
    );
};
